import { FIX_HEADER, SET_DARK_MODE, SET_LIGHT_MODE, SET_VIEWPORT_LAYOUT, TOGGLE_SEARCH_BAR, UNFIX_HEADER } from "../actionCreators/actionTypes";

export const GENERAL_LAYOUT = "";
export const PASSPHRASE_LAYOUT = "passphrase layout";

export const DARK_MODE = "dark mode";
export const LIGHT_MODE = "light mode";
export const HEADER_FIXED = "header fixed";
export const HEADER_UNFIXED = "header unfixed";


const initState = {
    viewportLayout: GENERAL_LAYOUT,
    illumMode: LIGHT_MODE,       //illumination mode. dark or
    showSearchBar: false,
    headerPositon: HEADER_UNFIXED,        //fixed, unfixed
}

const UIUXReducer = (state = initState, action) => {
    switch (action.type) {
        case SET_VIEWPORT_LAYOUT:
            return {
                ...state,
                viewportLayout: action.layout
            };
        case SET_LIGHT_MODE:
            return {
                ...state,
                illumMode: LIGHT_MODE
            };
        case SET_DARK_MODE:
            return {
                ...state,
                illumMode: DARK_MODE
            };
        case FIX_HEADER:
            return {
                ...state,
                headerPositon: HEADER_FIXED
            };
        case UNFIX_HEADER:
            return {
                ...state,
                headerPositon: HEADER_UNFIXED
            }
        case TOGGLE_SEARCH_BAR:
            return {
                ...state,
                showSearchBar: state.showSearchBar ? false : true
            }
        default:
            return state;
    }
}

export default UIUXReducer;