import { IMPORT_KEYSTORE_JSON_FAIL, IMPORT_KEYSTORE_JSON_START, IMPORT_KEYSTORE_JSON_SUCCESS, IMPORT_MNEMONIC_PHRASE_FAIL, IMPORT_MNEMONIC_PHRASE_START, IMPORT_MNEMONIC_PHRASE_SUCCESS, IMPORT_NETWORK_ERROR, IMPORT_NET_ERR_REFRESH, IMPORT_PRIVATE_KEY_FAIL, IMPORT_PRIVATE_KEY_START, IMPORT_PRIVATE_KEY_SUCCESS } from "../actionCreators/actionTypes";

const initState = {
    mnemonicPhrase: {
        start: false,
        success: false,
        errors: null
    },

    keyStoreJSON: {
        start: false,
        success: false,
        errors: null
    },

    privateKey: {
        start: false,
        success: false,
        errors: null
    },

    netError: null
};

const ImportReducer = (state = initState, action) => {
    switch (action.type) {
        case IMPORT_NET_ERR_REFRESH:
            return {
                mnemonicPhrase: {
                    start: false,
                    success: false,
                    errors: null
                },
            
                keyStoreJSON: {
                    start: false,
                    success: false,
                    errors: null
                },
            
                privateKey: {
                    start: false,
                    success: false,
                    errors: null
                },
                netError: null
            }
        case IMPORT_NETWORK_ERROR:
            return {
                ...state,
                netError: action.error
            };
        case IMPORT_MNEMONIC_PHRASE_START:
            return {
                ...state,
                mnemonicePhrase: {
                    ...state.mnemonicPhrase,
                    start: true,
                    success: false,
                    errors: null
                },
                netError: null
            };
        case IMPORT_MNEMONIC_PHRASE_FAIL:
            return {
                ...state,
                mnemonicPhrase: {
                    ...state.mnemonicPhrase,
                    start: false,
                    success: false,
                    errors: action.inputError ? action.inputError : null
                },
                netError: null
            };
        case IMPORT_MNEMONIC_PHRASE_SUCCESS:
            return {
                ...state,
                mnemonicPhrase: {
                    ...state.mnemonicPhrase,
                    start: false,
                    success: true,
                    errors: null
                },
                netError: null
            };
        case IMPORT_KEYSTORE_JSON_START:
            return {
                ...state,
                keyStoreJSON: {
                    ...state.keyStoreJSON,
                    start: true,
                    success: false,
                    errors: null
                },
                netError: null
            };
        case IMPORT_KEYSTORE_JSON_FAIL:
            return {
                ...state,
                keyStoreJSON: {
                    ...state.keyStoreJSON,
                    start: false,
                    success: false,
                    errors: action.inputError ? action.inputError : null
                },
                netError: null
            };
        case IMPORT_KEYSTORE_JSON_SUCCESS:
            return {
                ...state,
                keyStoreJSON: {
                    ...state.keyStoreJSON,
                    start: false,
                    success: true,
                    errors: null
                },
                netError: null
            };
        case IMPORT_PRIVATE_KEY_START:
            return {
                ...state,
                privateKey: {
                    ...state.privateKey,
                    start: true,
                    success: false,
                    errors: null
                },
                netError: null
            };
        case IMPORT_PRIVATE_KEY_FAIL:
            return {
                ...state,
                privateKey: {
                    ...state.privateKey,
                    start: false,
                    success: false,
                    errors: action.inputError ? action.inputError : null

                },
                netError: null
            };
        case IMPORT_PRIVATE_KEY_SUCCESS:
            return {
                ...state,
                privateKey: {
                    ...state.privateKey,
                    start: false,
                    success: true,
                    errors: null
                },
                netError: null
            };
        default:
            return state;
    }
};

export default ImportReducer;